
import {_} from '@/libs/util'
import util from '@/libs/util'
import common from '@/libs/common.mixin'
import Card from 'vux/src/components/card/index.vue'

import MyRater from 'c%/MyRater.vue'
import XFilter from 'c%/Filter.vue'

const list = [
{dict: 'grade',model: 'school_grade', title: '年级',default: []},
{dict: 'customer_status',model: 'customer_status_did',title: '客户状态',default: []},
{dict: 'from',model: 'from_did',title: '招生来源',default: []},
{com: 'SelectEmployee',model: 'follow_eid',title: '主责任人',default: 0},
{com: 'SelectEmployee',model: 'second_eid',title: '副责任人',default: 0}
]

export default{
mixins: [ common ],
components: {
Card,
MyRater,
XFilter
},
data () {
return {
filter: {
is_reg: 0,
name: '',
from_did: [],
school_grade: [],
customer_status_did: [],
follow_eid: 0,
second_eid: 0
},
data: [],
dictList: list,
}
},
methods: {
showFilter () {
this.$refs['filter'].show()
},
},
computed: {
filter_data () {
let data = this.data,
val = this.filter.name
if(data&&data.length){
if(!/^\s*$/.test(val)){
let filter = (item) => {
return item.pinyin_abbr.indexOf(val)>-1||item.pinyin.indexOf(val)>-1||item.name.indexOf(val)>-1
}
return data.filter(filter)
}
}

return this.data
},
data_uri () {
let filter = this.filter
let params = 'is_reg='+filter.is_reg

params += '&follow_eid='+ this.eid$

for(let i in filter){
let val = filter[i]
if(Array.isArray(val)){
if(val.length){
params += util.sprintf('&%s=[In,%s]',i,val.join(','))
}
}else{
if(val){
params += util.sprintf('&%s=%s',i,val)
}
}
}

return 'customers?'+params
}
}
}



